.hero {
    position: relative;
    height: 450px;
    padding-top: 50px;

    @media screen and (min-width:480px) {
        padding-top: 40px;
    }

    @media screen and (min-width:768px) {
        height: 349px;
        padding-top: 14px;
    }

    @media screen and (min-width:1200px) {
        height: 636px;
        padding-top: 45px;

    }


}

.hero__container--top {
    display: flex;
    justify-content: space-between;

}

.hero__title {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.26;
    letter-spacing: 0.04em;
    max-width: 247px;
    // margin-right: auto;

    @media screen and (min-width:1200px) {
        font-size: 38px;
        line-height: 1.2;
        max-width: 347px;
    }


}

.hero-list__numbers {
    display: none;

    @media screen and (min-width:768px) {
        display: flex;
        flex-direction: column;
        padding-top: 10px;
        text-align: right;

    }

    @media screen and (min-width:1200px) {
        padding-top: 8px;

    }

}

.hero-list__numbers .hero-list__item+.hero-list__item {
    @media screen and (min-width:768px) {
        margin-top: 20px;
    }

    @media screen and (min-width:1200px) {
        margin-top: 16px;
    }
}

.hero-list__item>span {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 900;
    font-size: 28px;
    line-height: 1.2;

    letter-spacing: 0.04em;

    color: $tittle-text-color;

    @media screen and (min-width:1200px) {
        font-size: 46px;
    }
}

.hero-list__paragraph {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;


    text-align: right;

    color: $white-color;

    @media screen and (min-width:1200px) {
        font-size: 14px;
        line-height: 1.2;
    }
}

.hero__container--left {
    padding-top: 32px;

    @media screen and (min-width:768px) {
        padding-top: 0;

    }

    @media screen and (min-width:1200px) {
        padding-top: 28px;

    }

}

.hero__button {
    display: inline-flex;
    align-items: center;
    text-align: center;
    color: $tittle-text-color;
    background-color: transparent;
    margin-right: 12px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;

    border: 2px solid $accent-color;
    border-radius: 10px;
    padding: 12px 32px;




}

.hero__link {
    display: inline-flex;
    align-items: center;
    text-align: center;
    text-decoration-line: underline;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    color: $accent-color;

}

.hero__container--bottom {
    display: none;

    @media screen and (min-width:768px) {
        display: flex;
        position: relative;

    }

}

