.hero-bottom__img {
    margin-left: 0;
    display: none;

    @media screen and (min-width:768px) {
        display: flex;
        position: absolute;
        z-index: 2;
       
        top: 187px;

    }

    @media screen and (min-width:1200px) {
       
        top: 347px;
    }
}

.hero-bottom__paragraph {
    display: none;

    @media screen and (min-width: 768px) {
        display: block;
        position: absolute;
        
        top: 210px;
        left: 223px;
        width: 139px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 1.2;
        color: $primary-text-color;
       

    }

    @media screen and (min-width:1200px) {
        width: 200px;
        font-size: 14px;
        line-height: 1.42;
        left: 257px;
        top: 421px;
    }

}

.hero-bottom__svg--arrow {
    width: 40px;
    height: 16px;
    bottom: 0;
    fill: $white-color;

    @media screen and (min-width:768px) {
        margin-top: 12px;
    }

    @media screen and (min-width:1200px) {
        margin-top: 24px;
    }
}

.hero-points {
    position: absolute;
    bottom: 0;
    z-index: -1;

    @media screen and (max-width:479px) {
        left: 0;
    }

    @media screen and (min-width: 480px) and (max-width: 767px) {
        right: 0;
    }

    @media screen and (min-width: 768px) {
        width: 100%;
        left: 0;
    }


    @media screen and (min-width:1200px) {
        bottom: 0;
    }

}

.hero-one {
    position: absolute;
    bottom: 0;
    left: 93px;

    @media screen and (min-width: 480px) {
        left: 243px;
    }

    @media screen and (min-width: 768px) {
        left: 408px;
    }

    @media screen and (min-width: 1200px) {
        left: 652px;
        bottom: 0;
    }
}